<template>
  <div class="container">
    <h2>登入狀態</h2>
    <div v-if="user">
      <p>當前用戶：{{ user.displayName }}</p>
      <p>電子郵件：{{ user.email }}</p>
      <p>電話號碼：{{ user.phoneNumber }}</p>
      <p>剩餘時間：{{ expirationTime }}</p> <!-- 新增顯示剩餘時間的元素 -->
      <button @click="logout">登出</button>
    </div>
    <div v-else>
      <p>未登入</p>
      <button @click="login">登入</button>
    </div>
  </div>
</template>

<script>
import 'firebase/compat/auth';
import { auth } from '@/firebaseConf';
import { getAuth, onAuthStateChanged } from "firebase/auth";

export default {
  data() {
    return {
      user: null,
      expirationTime: null // 新增剩餘時間的資料屬性
    };
  },
  mounted() {
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.$store.dispatch("setLlogin",true);
        // User is signed in, see docs for a list of available properties
        // https://firebase.google.com/docs/reference/js/auth.user
        const uid = user.uid;
        this.user=user;
        alert(uid);
        // ...
        this.updateExpirationTime(); // 更新剩餘時間
      } else {
        alert("._.");
      }
    });
  },
  methods: {
    login() {
      alert(this.user.uid);
    },
    logout() {
      auth.signOut();
    },
    updateExpirationTime() {
      const auth = getAuth();
      auth.currentUser.getIdTokenResult()
        .then(idTokenResult => {
          this.expirationTime = idTokenResult.expirationTime;
        })
        .catch(error => {
          alert("無法獲取憑證到期時間：", error);
        });
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-family: Arial, Helvetica, sans-serif;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}
</style>
